@use 'global.module.scss' as *;

#detailedTable {
    background-color: white;
    margin: 15px;
    position: absolute;
    width: 1475px;
    top: 300px;
    opacity: 0;
    border-radius: 12px;
    box-shadow: 0 1px 4px 1px rgba(0, 0, 0, .18);
    overflow: hidden;
}

#detailedTableBanner {
    margin: 0;
    background-color: $primaryAccent;
    width: 100%;
    height: 60px;
    font-size: 15pt;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 12px 12px 0 0;
}

#filters {
    margin: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.filter {
    margin: 12px;
    width: 180px;
    display: flex;
    flex-direction: row;
    align-items: center;

    p {
        margin: 0 6px 0 0;
        text-align: center;
    }
}

.filterInput {
    width: 120px;
    height: 30px;
    margin: 0 auto;
    padding: 0 0 0 9px;
    border: none;
    border-radius: 6px;
    box-shadow: 0 1px 4px 1px rgba(0, 0, 0, .18);

    &::placeholder {
        font-style: italic;
    }

    &:focus {
        outline: $primaryColor auto 1px;
    }
}

.tableDiv {
    margin: 0;
    padding: 0;
    font-family: Arial, Helvetica, sans-serif;
    overflow: scroll;
    height: 900px;

    &::-webkit-scrollbar {
        -webkit-appearance: none;
    }

    &::-webkit-scrollbar:vertical {
        width: 12px;
    }

    &::-webkit-scrollbar:horizontal {
        height: 12px;
    }

    &::-webkit-scrollbar-thumb {
        border-radius: 8px;
        border: 2px solid white;
        background-color: rgba(0, 0, 0, .27);
    }

    &::-webkit-scrollbar-track {
        background-color: white;
    }
}

table {
    margin: 0;
    border-spacing: 0;
    width: 100%;
    opacity: 0;

    thead {
        background-color: white;
        margin: 0;
        position: sticky;
        top: -1px;
        height: 60px;
        user-select: none;
        cursor: grab;
    }

    tbody {
        margin: 0;
    }

    th,
    td {
        font-size: 10pt;
        margin: 0;
        padding: 6px;
        border-bottom: 1px solid #ddd;
    }

    th {
        position: relative;
        font-size: 12pt;
        height: 60px;
        padding: 6px 12px;
        border-bottom: 3px solid #ddd;
    }
}

.link {
    color: $primaryColor;
    text-decoration: none;
}

.handle {
    position: absolute;
    top: 0;
    right: -2px;
    width: 4px;
    height: 100%;
    cursor: col-resize;
    opacity: 0;

    .handle:hover {
        background-color: black;
        opacity: 1;
    }
}


#tableControls {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0 12px;
    height: 69px;
    width: 100%;
    border-top: 1px solid #ddd;
}

#amountOfRowsToDisplay {
    display: flex;
    align-items: center;
    margin: 0 12px 0 0;
}

#rowAmountSelect {
    width: 60px;
    height: 24px;
    margin: 0 12px 0 0;
    border: none;
    border-bottom: 2px solid black;
}

#rowAmountSelect:focus {
    outline: rgba(0, 0, 0, 0) auto 1px;
    border-bottom: 2px solid #007097;
}

.tableButton {
    background-color: white;
    border: none;
    padding: 0 12px;
    height: 40px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    margin: 0 12px 0 0;
    cursor: pointer;
    border-radius: 6px;
    box-shadow: 0 1px 4px 1px rgba(0, 0, 0, .18);
    transition: all 0.1s ease-in-out;

    &:hover {
        scale: 1.05;
        box-shadow: 0 3px 4px 1px rgba(0, 0, 0, .18);
        transition: all 0.1s ease-in-out;
    }

    &:active {
        scale: 1;
        box-shadow: inset 0 1px 2px 1px rgba(0, 0, 0, .18);
        transition: all 0.1s ease-in-out;
    }
}


#viewRowSearch {
    width: 120px;
    height: 40px;
    padding: 0 12px;
    margin: 0;
    border: none;
    border-radius: 6px;
    box-shadow: 0 1px 4px 1px rgba(0, 0, 0, .18);

    &::placeholder {
        font-style: italic;
    }

    &:focus {
        outline: #007097 auto 1px;
    }
}


#currentRowIndexCount {
    display: flex;
    align-items: center;
    margin: 0 12px 0 auto;
}