@use "global.module.scss" as *;

#feedback {
    background-color: white;
    margin: 15px 0 0 0;
    padding: 6px;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    border-radius: 12px;
    box-shadow: 0 1px 4px 1px rgba(0, 0, 0, .18);

    #thumbsUp {
        margin: 0;
    }

    #review {
        margin: 0;
    }

    .feedbackIcon {
        color: $toolColor;
        padding: 12px;
        font-size: 24pt;
        position: relative;
        cursor: pointer;
        border-radius: 50%;
        transition: all 0.2s ease-in-out;

        &:hover {
            scale: 1.05;
            color: $primaryColor;
            background-color: $toolHover;
            transition: all 0.2s ease-in-out;
        }

        &:active {
            background-color: $toolActive;
            transition: all 0.2s ease-in-out;
        }
    }
}